<script land="script" scoped>
import Backtop from '@/components/backtop/backtop' // 返回顶部
// import { pageRouter } from '@/common/js/mixin.js'

export default {
  name: 'BidGysHall',
  components: {
    Backtop
  },
  // mixins: [pageRouter],
  data() {
    return {
      activeStep: 0, // 用作添加点击后的类名
      flag: false, // true是固定定位
      btnScrollTop: 0, // 导航条距离顶部的距离
      current: 0,
      slide: [
        {
          imgUrl: require('@/assets/bid-images/01.png')
        },
        {
          imgUrl: require('@/assets/bid-images/01.png')
        },
        {
          imgUrl: require('@/assets/bid-images/01.png')
        },
        {
          imgUrl: require('@/assets/bid-images/01.png')
        }
      ],
      show: false,
      show1: false,
      show2: false,
      show3: false,
      show4: false,
      date: '2020年2月20日',
      columns: ['普通税', '增值税'],
      columns1: ['7%', '15%', '20%'],
      columns2: ['交付方式1', '交付方式2', '交付方式3'],
      minDate: new Date(2020, 0, 1),
      maxDate: new Date(2025, 10, 1),
      currentDate: new Date()
    }
  },
  mounted() {
    const btnScrollTop = document.querySelector('.operation-btn').offsetTop
    this.btnScrollTop = btnScrollTop // 挂载后获取到导航条距离顶部的距离，然后给data.btnScrollTop

    // 监听滚动条滚动的距离，超过导航条距离顶部的距离则添加相应类名，小于这删除相应类名
    document.addEventListener('scroll', () => {
      const target = document.documentElement.scrollTop
      if (target >= this.btnScrollTop) {
        this.flag = true
      } else {
        this.flag = false
      }
    })
  },
  methods: {
    onChange(index) {
      this.current = index
    },
    showszPopup() {
      this.show = true
    },
    showslPopup() {
      this.show1 = true
    },
    showjhqPopup() {
      this.show2 = true
    },
    showjffsPopup() {
      this.show3 = true
    },
    showcjPopup() {
      this.show4 = true
    },
    goMsglist() {
      this.$router.push({
        name: 'JjMsgList'
      })
    },
    goSuplist() {
      this.$router.push({
        name: 'JjSupList'
      })
    },
    ConfirmCj() {
      this.$dialog.confirm({
        title: '提示',
        message: '确认提交报价吗？'
      }).then(() => {
        // this.$router.push('/login')
      }).catch(() => {
        // on cancel
      })
    },

    SendXx() {
      this.$toast.success('消息发送成功')
    },

    // 点击进行锚点定位
    goTarget(index) {
      var name = '#section-' + (index + 1)// 需要跳转的id
      const target = document.querySelector('body')
      var anchor = this.$el.querySelector(name)
      // 判断滚动条是否滚动到底部
      if (target.scrollHeight <= target.scrollTop + target.clientHeight) {
        this.activeStep = index
      }
      document.documentElement.scrollTop = anchor.offsetTop - 60
    }
  }
}
</script>

